:root {
  --font-body: Arial, sans-serif;
}

:root {
  --facebook: hsl(221, 44%, 41%);
  --brand: hsl(88, 50%, 53%);
  --orange: hsl(12, 81%, 57%);
  --pinterest: hsl(358, 73%, 46%);
  --white: hsl(0, 0%, 100%);
  --gray-light: hsl(0, 0%, 96%);
  --gray: hsl(0, 0%, 88%);
  --gray-dark: hsl(0, 0%, 46%);
  --black: hsl(60, 2%, 11%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a,
p,
body {
  margin: 0;
  padding: 0;
  color: var(--gray-dark);
  font-family: "Lato", sans-serif;
  transition: color 0.2s ease-in-out;
}

input {
  background-color: transparent;
  padding: 0.625rem 0.9375rem;
}

img {
  border-radius: 0.3125rem 0.3125rem 0 0;
}

.item {
  text-align: center;
  border-radius: 0.3125rem;
  border: 0.0625rem solid var(--gray);
}
.item__title {
  font-size: 0.875rem;
  color: var(--black);
  margin: 0.5rem 0;
}
.item__title--lg {
  font-size: 1rem;
}
.item__title img {
  float: right;
}

.logo {
  display: inline-block;
}

.input {
  float: left;
  min-width: 100%;
  border-radius: 0.3125rem;
  border: 0.0625rem solid var(--gray);
}
.input--sm {
  min-width: 20.3125rem;
  border-radius: 0.3125rem 0 0 0.3125rem;
}

.button {
  cursor: pointer;
  color: var(--white);
  background-color: var(--brand);
  border: 0.0625rem solid var(--brand);
  transition: all 0.2s ease-in-out;
  padding: 0.625rem 1.875rem;
  border-radius: 0.3125rem;
  position: relative;
}
.button:hover {
  color: var(--white);
  border-color: #71a437;
  background-color: #71a437;
}
.button__transparent {
  color: var(--gray-dark);
  border-color: var(--gray-dark);
  background-color: transparent;
}
.button__transparent:hover {
  background-color: unset;
}
.button--alert::after {
  content: "9+";
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  font-size: 0.75rem;
  min-width: 1.5625rem;
  min-height: 1.5625rem;
  color: var(--white);
  background-color: var(--brand);
  position: absolute;
  top: -0.625rem;
  right: -0.625rem;
}
.button--sm {
  padding: 0.625rem 0.9375rem;
}
.button--lg {
  font-weight: 600;
  font-size: 1rem;
  padding: 0.9375rem 3.125rem;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

a:hover,
.button__transparent:hover {
  color: var(--brand);
}

.image {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 12.25rem;
}
.image:not(.image--sm) img {
  flex-grow: 1;
}

.text {
  display: block;
  text-align: left;
  padding: 1.25rem 1.25rem 0.9375rem;
}

.title {
  font-size: 1.5rem;
}
.title--lg {
  font-weight: 400;
  font-size: 1.875rem;
  color: var(--brand);
}

.subtitle {
  color: var(--brand);
}
.subtitle--lg {
  font-size: 1.1875rem;
}

.division {
  margin: 2.5rem 0;
  position: relative;
  max-height: 0.0625rem;
  background-color: rgba(117, 117, 117, 0.1);
}
.division::after {
  content: "or";
  padding: 0.3125rem;
  background-color: #fff;
  position: relative;
  top: -0.625rem;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.container {
  padding: 0 0.625rem;
  max-width: 81.25rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.popup {
  position: fixed;
  z-index: 2;
  min-width: 100%;
  min-height: 100vh;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
}
.popup:target {
  visibility: visible;
  opacity: 1;
}
.popup__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  max-width: 31.25rem;
  border-radius: 0.3125rem;
  padding: 3.75rem 3.125rem 1.5625rem;
  border: 0.0625rem solid var(--gray-dark);
  transform: translate(-50%, -50%);
  background-color: #fff;
}
.popup__inner *:not(.popup__remember, :last-child) {
  margin-bottom: 1.25rem;
}
.popup__inner .close {
  position: absolute;
  top: 0.9375rem;
  right: 0.9375rem;
}
.popup__inner .division:nth-child(6) {
  margin: 2.5rem -3.125rem;
}
.popup__inner .division:nth-child(6)::after {
  opacity: 0;
}
.popup__help {
  display: flex;
  justify-content: space-between;
  min-width: 100%;
}
.popup__buttons {
  font-size: 0;
  padding-top: 0.5rem;
}
.popup__buttons *:not(:last-child) {
  margin-right: 1.125rem;
}
.popup__remember input {
  display: none;
}
.popup__remember label {
  position: relative;
  cursor: pointer;
}
.popup__remember label:before {
  content: "";
  padding: 0.5rem;
  margin-right: 0.3125rem;
  border-radius: 0.3125rem;
  display: inline-block;
  border: 0.0625rem solid var(--gray);
  vertical-align: middle;
  position: relative;
}
.popup__remember input:checked + label:after {
  content: "";
  min-width: 0.375rem;
  min-height: 0.625rem;
  transform: rotate(45deg);
  border: 0.0625rem solid var(--brand);
  border-width: 0 0.125rem 0.125rem 0;
  position: absolute;
  top: 0.3125rem;
  left: 0.375rem;
}
.popup__social {
  font-size: 0;
}
.popup__social p {
  font-size: 0.875rem;
  color: var(--black);
}
.popup__social a {
  padding: 0.9375rem;
  border-radius: 50%;
  min-width: 3.125rem;
  min-height: 3.125rem;
  display: inline-block;
  margin-right: 1.875rem;
  background-color: var(--facebook);
}
.popup__social a:nth-child(3) {
  background-color: var(--orange);
}
.popup__social a:nth-child(3) img {
  max-width: 1.25rem;
  min-height: 1.25rem;
}
.popup__social a:nth-child(4) {
  background-color: var(--pinterest);
  margin-right: 0;
}
.popup__social a img {
  min-width: 0.9375rem;
  max-height: 1.09375rem;
  transform: scale(1.5);
}
.popup__login {
  font-size: 0.875rem;
}
.popup__login .button {
  color: var(--brand);
  margin-left: 1.25rem;
  border-color: var(--brand);
}

.dropdown {
  position: relative;
}
.dropdown:hover .dropdown__item {
  display: block;
}
.dropdown__item {
  display: none;
  background-color: var(--white);
  box-shadow: 0 0.625rem 1.25rem var(--gray-dark);
  padding: 1.875rem 1.875rem 2.5rem;
  position: absolute;
  top: 2.5rem;
}
.dropdown__content, .dropdown__list {
  display: flex;
  justify-content: space-between;
}
.dropdown__content {
  align-items: center;
}
.dropdown__list {
  min-width: 38.75rem;
}
.dropdown__list li:not(:last-child) {
  margin-bottom: 0.9375rem;
}
.dropdown .title {
  color: var(--black);
  margin-bottom: 0;
}
.dropdown .division {
  margin: 1.25rem 0;
  background-color: var(--brand);
}
.dropdown .division::after {
  opacity: 0;
}

.header__login {
  margin-bottom: 1.875rem;
}
.header__top, .header__bottom nav {
  display: flex;
  align-items: center;
}
.header__top {
  justify-content: space-between;
  min-height: 6.25rem;
}
.header__top button {
  border-radius: 0 0.3125rem 0.3125rem 0;
}
.header__bottom {
  position: relative;
  background-color: var(--gray-light);
  z-index: 1;
}
.header__bottom nav *:not(.dropdown *, :last-child) {
  margin-right: 1.875rem;
}
.header__bottom nav *:not(.dropdown *:not(.dropdown > a)) {
  padding: 0.8125rem 0;
}
.header__buttons {
  display: flex;
  align-items: center;
}
.header__buttons-login > *:last-child::after {
  content: "";
  margin-left: 0.5rem;
  border: 0.3125rem solid transparent;
  border-top-color: var(--gray-dark);
}
.header__buttons *:not(:last-child) {
  margin-right: 1.5625rem;
}

.hero {
  background: url("../images/background.jpg") no-repeat 50%/cover;
  text-align: center;
  position: relative;
  color: var(--white);
  flex-grow: 1;
}
.hero__inner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.hero__title {
  line-height: 1.4;
  font-weight: 300;
  font-size: 3.125rem;
}

.seller {
  margin-top: 18.25rem;
  margin-bottom: 7.25rem;
}
.seller__items {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.seller__item {
  max-width: 23.75rem;
  margin-bottom: 2.5rem;
}
.seller .explore {
  text-align: center;
}

.swiper {
  box-shadow: 0 0.75rem 4.25rem var(--gray-dark);
  padding: 1.875rem 4.375rem 2.8125rem;
  background-color: #fff;
  border-radius: 0.3125rem;
  position: absolute;
  max-width: 100%;
  top: -6.25rem;
}
.swiper-slide {
  text-align: center;
  transition: color 0.2s ease-in-out;
}
.swiper-slide img {
  max-width: 100%;
}
.swiper-button-prev, .swiper-button-next {
  top: 1.375rem;
  width: 4.375rem;
  height: 100%;
}
.swiper-button-prev::after, .swiper-button-next::after {
  content: "";
}
.swiper-button-prev {
  left: 0;
  background: url("../images/arrow-left.svg") no-repeat 50%;
}
.swiper-button-next {
  right: 0;
  background: url("../images/arrow-right.svg") no-repeat 50%;
}

.info__item {
  display: flex;
  align-items: end;
  justify-content: space-between;
  max-width: 72.1875rem;
  margin-bottom: 9.25rem;
}
.info__item:last-child {
  margin-bottom: 7.5rem;
  align-items: center;
  margin-left: auto;
}
.info__text {
  max-width: 37.5rem;
}
.info__text p {
  margin-bottom: 1.125rem;
}

.grid {
  margin-bottom: 4.375rem;
}
.grid:not(.grid--sm) {
  margin-bottom: 8.875rem;
}
.grid__inner {
  display: grid;
  gap: 4.375rem;
}
.grid--lg .grid__inner {
  grid-template: 18.75rem 11.25rem 3.3125rem 11.25rem repeat(2, 18.75rem)/repeat(4, 1fr);
}
.grid--sm .grid__inner {
  grid-template: 24.1875rem/repeat(3, 1fr);
}

.item {
  overflow: hidden;
  border: 0.0625rem solid var(--gray);
}
.item--lg, .item--high {
  grid-row: span 2;
}
.item--lg {
  grid-column: span 2;
}

.footer__items {
  display: flex;
  justify-content: space-evenly;
  border-bottom: 0.0625rem solid var(--gray-dark);
  padding-bottom: 2rem;
}
.footer__items > *:last-child li {
  position: relative;
}
.footer__items > *:last-child li:nth-child(1) a::before {
  background-image: url("../images/facebook.svg");
}
.footer__items > *:last-child li:nth-child(2) a::before {
  background-image: url("../images/pinterest.svg");
}
.footer__items > *:last-child li:nth-child(3) a::before {
  background-image: url("../images/twitter.svg");
}
.footer__items > *:last-child li:nth-child(4) a::before {
  background-image: url("../images/instagram.svg");
}
.footer__items > *:last-child li a {
  padding-left: 1.75rem;
}
.footer__items > *:last-child li a::before {
  content: "";
  border-radius: 50%;
  min-width: 1.125rem;
  min-height: 1.125rem;
  background: var(--brand) no-repeat 50%;
  position: absolute;
  left: 0;
}
.footer__item * {
  font-weight: 700;
  color: var(--black);
}
.footer__item * li {
  font-size: 0.875rem;
}
.footer__item * li:not(:last-child) {
  margin-bottom: 1.25rem;
}
.footer__copyright {
  text-align: center;
  padding: 1.25rem 0;
  font-size: 0;
}
.footer__copyright * {
  font-size: 1rem;
}
.footer__copyright *:not(:last-child) {
  margin-right: 1.25rem;
}